import './App.css';
import 'react-datepicker/dist/react-datepicker.css';
import DatePicker, { registerLocale } from 'react-datepicker';
import ru from 'date-fns/locale/ru';
import { useForm, Controller } from 'react-hook-form';
import axios from 'axios';
import regions from './utils/regions [MConverter.eu].js';
registerLocale('ru', ru);

function Login() {
  const regArrays = Object.entries(regions);
  const {
    register,
    handleSubmit,
    formState: { errors },
    reset,
    control,
  } = useForm({
    mode: 'onChange',
    defaultValues: {
      region: 'Ваш регион',
      sex: 'Select...',
      has_children_below_12: 'Select...',
      family_status: 'Select...',
    },
  });

  function onSubmit(data) {
    let sendBody = {
      birthday_at: data.birthday_at,
      sex: data.sex,
      family_status: data.family_status,
      region: data.region,
      has_children_below_12:
        data.has_children_below_12 === 'Yes' ? true : false,
      tourism_goals: data.tourism_goals,
    };
    let sendEmail = data.email;

    console.log(JSON.stringify(sendBody));
    async function sendData() {
      try {
        await axios({
          method: 'POST',
          url: apiURL,
          params: {
            email: sendEmail,
          },
          headers: { 'Content-Type': 'application/json' },
          data: JSON.stringify(sendBody),
        });
      } catch (error) {
        console.log(error);
      }
    }

    const apiURL = 'https://api.recommender.hrb.software/questionnaire';

    sendData();
    reset();
  }
  let year = new Date();
  let check18 = year.setFullYear(year.getFullYear() - 18);

  return (
    <div className="form">
      <form onSubmit={handleSubmit(onSubmit)}>
        <div className="container">
          <h1>Анкета</h1>
          <div className="textField box">
            {' '}
            <label htmlFor="email">Ваш e-mail</label>
            <input
              placeholder="name@mail.com"
              type="text"
              {...register('email', {
                required: 'This is a required field',
                pattern: {
                  value:
                    /^[a-zA-Z0-9.!#$%&’*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/,
                  message: 'Неверный формат',
                },
              })}
            ></input>
            <div className="errMessage" style={{ height: 30 }}>
              {errors?.email && <p>{errors?.email?.message}</p>}
            </div>
          </div>

          <div className="region">
            <Controller
              control={control}
              rules={{ required: true }}
              name="region"
              render={({ field: { onChange, value } }) => (
                <select className="dropdown" onChange={onChange} value={value}>
                  {regArrays.map((regArray) => {
                    return (
                      <>
                        <option disabled selected hidden>
                          Ваш регион
                        </option>
                        <option class="n" key={regArray[0]} value={regArray[0]}>
                          {regArray[1]}
                        </option>
                      </>
                    );
                  })}{' '}
                </select>
              )}
            />
          </div>

          <div className="dateControl box">
            <label htmlFor="birthday_at">Дата рождения</label>
            <Controller
              control={control}
              rules={{ required: true }}
              name="birthday_at"
              render={({ field: { onChange, value } }) => (
                <DatePicker
                  dateFormat="dd/MM/yyyy"
                  placeholderText="ДД.ММ.ГГГГ"
                  onChange={onChange}
                  minDate={new Date('01-01-1900')}
                  peekNextMonth
                  showMonthDropdown
                  showYearDropdown
                  dropdownMode="select"
                  locale="ru"
                  selected={value}
                />
              )}
            />
          </div>
          <div className="sexChoose">
            <input
              {...register('sex', { required: true })}
              type="radio"
              value="male"
              id="male"
            />
            <label for="male"> Мужской</label>

            <input
              {...register('sex', { required: true })}
              type="radio"
              value="female"
              id="female"
            />
            <label for="female"> Женский</label>
          </div>
          <div className=" familyDiv">
            <label htmlFor="family_status">Семейное положение</label>
            <select {...register('family_status', { required: true })}>
              <option value="married">Женат/замужем</option>
              <option value="in_relationship">Состою в отношениях</option>
              <option value="single">Одинокий/одинокая</option>
            </select>
          </div>
          <div className=" familyDiv">
            <label htmlFor="has_children_below_12">
              Несовершеннолетние дети
            </label>
            <select {...register('has_children_below_12', { required: true })}>
              <option value="Yes">Да</option>
              <option value="No">Нет</option>
            </select>
          </div>
          <div className="tourismCheck">
            Вид туризма
            <input
              type="checkbox"
              value="active"
              id="active"
              {...register('tourism_goals', { required: 'Выберите варианты' })}
            />{' '}
            <label htmlFor="active">Активный</label>
            <input
              type="checkbox"
              value="calm"
              id="calm"
              {...register('tourism_goals', { required: 'Выберите варианты' })}
            />{' '}
            <label htmlFor="calm">Спокойный</label>
            <input
              type="checkbox"
              value="cultural"
              id="cultural"
              {...register('tourism_goals', { required: 'Выберите варианты' })}
            />{' '}
            <label htmlFor="cultural">Культурный</label>
            <input
              type="checkbox"
              value="social"
              id="social"
              {...register('tourism_goals', { required: 'Выберите варианты' })}
            />{' '}
            <label htmlFor="social">Социальный</label>
            <div className="errMessage">
              {errors.tourism_goals && <p>{errors.tourism_goals.message}</p>}
            </div>
          </div>
          <div className="submitDiv">
            <button className="submitButton" type="submit">
              Отправить
            </button>
          </div>
        </div>
      </form>
    </div>
  );
}

export default Login;
